@import url(https://fonts.googleapis.com/css?family=M+PLUS+Rounded+1c:400,500,700,800,900&display=swap);
/* font */

* {
	box-sizing: border-box;
	margin: 0px;
	padding: 0px;
	/* border: 1px red solid; */
}

html,
body {
	height: 100%;
	background-color: #faf1c9;
	font-family: 'M PLUS Rounded 1c', sans-serif;
	overflow-x: hidden;
	color: rgb(103, 81, 62);
	/* font-size: 1rem; */

	/* Avoid jumping due to width reflow - padding accounts for vert scrollbar */
	margin-left: calc(100vw - 100%);
}

#master-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 5%;
	position: relative;
}

h1,
h2 {
	letter-spacing: -0.5px;
	font-weight: 100;
	border-radius: 5px;
	color: rgb(40, 79, 0);
	background-color: rgb(218, 216, 136);
	display: inline-block;
	white-space: nowrap;
}

p {
	font-size: 18px;
}

/* Intro Section */

header {
	margin-bottom: 20px;
	display: flex;
	flex-direction: column;
}

.app-intro {
	display: flex;
	margin-top: 2em;
	margin-bottom: 3em;
}

/* App Title */

.app-intro .title-wrapper {
	padding-bottom: 1em;
}

.title-wrapper * {
	font-size: 1.1rem;
	margin-bottom: 0.75em;
	padding: 0px 0.25em;
}

#app-title {
	font-size: 2rem;
	margin-bottom: 0;
}

.intro-copy {
	flex: 1 1;
	margin-right: 10%;
}

.intro-copy p {
	margin-bottom: 20px;
}

#acnh-logo {
	max-width: 12%;
	min-width: 10%;
	position: absolute;
	right: 40px;
	top: 20px;
	/* transform: rotate(20deg); */
	/* TODO: Add swing interaction on hover */
}

main {
	display: flex;
	flex-direction: column;
	align-items: center;
	flex-wrap: wrap;
	width: 100%;
}

/* Class added to body when detailsview modal is open */
.scroll-lock {
	height: 100vh;
	overflow-y: hidden;
}

@media only screen and (max-width: 768px) {
	.master-container {
		padding: 40px;
	}

	#acnh-logo {
		display: none;
	}

	.app-intro {
		flex-direction: column;
	}

	.intro-copy {
		align-items: center;
	}
}

/* Wrap filter bar */

.filters-bar {
	display: flex;
	min-width: 100%;
	padding: 10px;
	margin-bottom: 20px;
	/* add relative position to try and get horizontal line behind content */
	position: relative;
	justify-content: space-between;
}

#line {
	position: absolute;
	top: 50%;
	bottom: 50%;
	left: 0;
	right: 0;
	background: rgb(92, 85, 60);
	opacity: 0.5;
	height: 3px;

	/* Extend line beyond parent by voiding margins */
	margin-left: -70px;
	margin-right: -70px;
	/* z-index: -1; */
}

.filters-bar .species,
.filters-bar .availability {
	display: flex;
	height: 100%;
	padding: 2px;
}

/* Species toggles | buttons */
.btn-wrapper {
	display: flex;
	position: relative;
	align-self: center;
	height: 100%;
}

/* Style Species Filter Buttons */
.filters-bar button {
	margin: 0px -3px;
	height: 60px;
	width: 60px;
	background-color: white;
	color: rgb(92, 85, 60);
	padding: 5px;
	border-radius: 50%;
	border: none;
}

.filters-bar button i {
	font-size: 1.75rem;
	/* opacity: 0.5; */
}

.filters-bar button:hover {
	transform: scale(1.15);
}

/* Icon inside button */
.filter-icon {
	cursor: pointer;
	width: 100%;
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
}

.tooltip {
	/* layout */
	position: absolute;
	bottom: 105%;
	left: 50%;
	transform: translateX(-50%);
	white-space: nowrap;
	text-align: center;

	/* style */
	background: rgb(254, 203, 77);
	border-radius: 5px;
	padding: 4px 8px;
	color: rgb(92, 85, 60);
	white-space: nowrap;
	box-shadow: 0 8px 6px -6px black;
}

@media only screen and (max-width: 768px) {
	.line {
		margin-left: -40px;
		margin-right: -40px;
	}
}

/* results table  */

.no-results {
	align-items: center;
	text-align: center;
	font-size: large;
}
.results-wrapper {
	display: flex;
	flex-grow: 1;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: -1px;
	margin-left: -1px;
}

.results-item {
	height: 100px;
	width: 100px;
	display: flex;
	justify-content: center;
	position: relative;
	outline: 1px rgb(92, 85, 60);
	outline-style: double;
	margin-top: 1px;
	margin-left: 1px;
}

.results-item:hover {
	cursor: pointer;
}

.search-img {
	image-rendering: crisp-edges;
	align-self: center;
	height: 80%;
	width: 80%;
	border: 2px dashed transparent;
}

.search-img:hover {
	border: 2px dashed rgb(92, 85, 60);
	transform: scale(1.1);
}

.name-tooltip {
	position: absolute;
	background: #f9f9f0;
	border-radius: 2px;
	padding: 5px;
	top: -20%;
	color: rgb(92, 85, 60);
	transform: rotate(-2deg);
	white-space: nowrap;
	display: inline;
	box-shadow: 0 8px 6px -6px black;
	/* Increase z-index, otherwise longer tooltip corner gets 'tucked' under outline of next results-item border */
	z-index: 1;
}

.name-tooltip span {
	background: #f9f9f0;
	font-size: 1rem;
	display: inline;
	border: 0.5px double rgb(92, 85, 60);
	padding: 3px;
}

#no-results-img {
	height: 50%;
}

/* detail modal */

/* Add darkened backdrop behind modal when opened */
#backdrop {
	display: flex;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
}

.critter-wrapper {
	/* center onscreen */
	margin: auto;

	/* layout */
	display: inline-flex;
	flex: 1 1;
	width: 960px;
	max-width: 80%;
	height: auto;
	min-height: 0;
	z-index: 3;

	/* style */
	background-color: #faf1c9;
	border: double rgb(153, 153, 153);
	border-radius: 5px;
}

.critter-wrapper-fish {
	/* center onscreen */
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	/* layout */
	display: flex;
	flex-direction: column;
	align-items: center;

	max-width: 960px;
	min-width: 0px;
	max-height: 90%;
	z-index: 1000;
	overflow-y: auto;

	/* style */
	background-color: #faf1c9;
	border: double rgb(153, 153, 153);
	border-radius: 5px;
}

/* Critter name label, image, and controls */
.critter-img-col {
	flex-direction: column;
	flex: 1 1;
	padding: 40px;
	width: 100%;
	text-align: center;
	border-right: double rgb(153, 153, 153);
}

.critter-wrapper-fish .critter-img-col {
	border-bottom: 4px double rgb(153, 153, 153);
}

/* Tag over critter image  */
.critter-name {
	background: #f9f9f0;
	border-radius: 2px;
	padding: 5px;
	color: rgb(92, 85, 60);
	white-space: nowrap;
	display: inline;
	box-shadow: 0 8px 6px -6px black;
}

.critter-name span {
	background: #f9f9f0;
	font-size: 1rem;
	display: inline;
	border: 0.5px double rgb(92, 85, 60);
	padding: 3px;
}

/* loaded image */
#critter-pic {
	height: auto;
	width: 100%;
	-webkit-filter: drop-shadow(0px 5px 5px black);
	        filter: drop-shadow(0px 5px 5px black);
}

/* Back, forward, and close controls*/

.img-controls {
	display: flex;
	justify-content: space-between;
}

#back,
#forward,
#close {
	font-size: 2rem;
	opacity: 0.75;
	color: rgb(103, 81, 62);
	cursor: pointer;
}

#back:hover,
#forward:hover,
#close:hover {
	opacity: 1;
}

/* Column with quote, location, price, etc */
.critter-details-col {
	flex-direction: column;
	flex: 1 1;
	padding: 40px;
	/* align-items: stretch;
	align-self: stretch; */
	width: 100%;
}

.critter-wrapper-fish .critter-details-col {
	flex-direction: row;
	flex: 1 1;
	padding: 40px;
	/* align-items: stretch;
	align-self: stretch; */
	width: 100%;
	min-height: 0;
}

#phrase {
	margin-bottom: 50px;
	font-weight: bold;
	font-style: italic;
	font-size: 1.5rem;
	line-height: 1;
	/* set height to prevent siblings from shifting down */
	height: 60px;
}

.critter-wrapper-fish #phrase {
	height: 50px;
	margin-bottom: 0;
}

.detail-wrapper {
	display: flex;
	flex-direction: row;
	margin-bottom: 1em;
}

.detail-wrapper > div {
	flex: 1 1;
	margin: 1em 0;
}

@media only screen and (max-width: 768px) {
	.critter-wrapper {
		flex-direction: column;
	}

	.critter-img-col {
		border-bottom: 4px double rgb(153, 153, 153);
	}

	#phrase {
		margin-bottom: 20px;
		font-weight: bold;
		font-style: italic;
		font-size: 1.2rem;
		line-height: 1;
		/* set height to prevent siblings from shifting down */
		height: 60px;
	}
}

/* Search Input */

.lang-search-wrapper {
	flex: 1 1;
}

.language-wrapper {
	margin-bottom: 20px;
}
.lang-search-wrapper > div {
	margin-bottom: 20px;
}
.control-wrapper {
	flex: 1 1;
	flex-wrap: wrap;
}

.search-form {
	margin-bottom: 20px;
}
.search-form input {
	background-color: transparent;
	border: none;
	border-bottom: 1.5px double rgb(92, 85, 60);
	margin: 5px 0px;
	font-size: medium;
	color: rgb(92, 85, 60);
	width: 80%;
}

::-webkit-input-placeholder {
	color: rgb(40, 79, 0);
}

:-ms-input-placeholder {
	color: rgb(40, 79, 0);
}

::-ms-input-placeholder {
	color: rgb(40, 79, 0);
}

::placeholder {
	color: rgb(40, 79, 0);
}

a {
	color: rgb(40, 79, 0);

	text-decoration: none;
}

@media only screen and (max-width: 768px) {
	.lang-search-wrapper,
	.control-wrapper {
		flex-direction: column;
	}
}

